    right: -25px;
    left: auto
}

.slick-prev:before {
    content: ''
}

[dir='rtl'] .slick-prev:before {
    content: ''
}

.slick-next {
    right: -25px
}

[dir='rtl'] .slick-next {
    right: auto;
    left: -25px
}

.slick-next:before {
    content: ''
}

[dir='rtl'] .slick-next:before {
    content: ''
}

.slick-dotted.slick-slider {
